* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  font-weight: 300;
  color: #121212;
}

.moduleContainer {
  max-width: 1440px;
  margin: auto;
  padding: 20px;
}
.moduleContainer img {
  width: 100%;
  height: auto;
}
.moduleContainer .moduleContent .title {
  font-size: 42px;
  font-weight: 700;
}
.moduleContainer .moduleContent .desc {
  margin-top: 12px;
  margin-bottom: 16px;
}
.moduleContainer .moduleContent button {
  width: 128px;
  height: 48px;
  padding: 16px 40px;
  background-color: #e4c1ae;
  border: none;
  border-radius: 32px;
  color: white;
  font-size: 16px;
  font-weight: 700;
  font-family: "Roboto", sans-serif;
  line-height: 100%;
}
.moduleContainer .moduleContent button:hover {
  cursor: pointer;
  background-color: #e4c1ae, 0.5;
  opacity: 0.9;
  box-shadow: 0px 0px 10px -1px #e4c1ae;
}

@media screen and (min-width: 768px) {
  .moduleContainer {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .moduleContainer .moduleContent {
    width: 380px;
  }
  .moduleContainer .moduleContent .desc {
    margin-top: 24px;
    margin-bottom: 32px;
  }
  .moduleContainer img {
    width: 42%;
    max-width: 505px;
    margin-right: 7%;
  }
}