.moduleContainer {
  max-width: 1440px;
  margin: auto;
  padding: 20px;

  img {
    width: 100%;
    height: auto;
  }

  .moduleContent {
    .title {
      font-size: 42px;
      font-weight: 700;
    }

    .desc {
      margin-top: 12px;
      margin-bottom: 16px;
    }

    button {
      width: 128px;
      height: 48px;
      padding: 16px 40px;
      background-color: $button-background;
      border: none;
      border-radius: 32px;
      color: white;
      font-size: 16px;
      font-weight: 700;
      font-family: 'Roboto', sans-serif;
      line-height: 100%;

      &:hover {
        cursor: pointer;
        background-color: $button-background, 0.5;
        opacity: 0.9;
        box-shadow: 0px 0px 10px -1px $button-background;
      }
    }
  }
}
